<template>
  <div class="shop">
    <div class="special-offer"></div>
    <div class="shop-content">
      <h2>Top sellers</h2>
      <ul>
        <li
          class="preview"
          v-for="item in items"
          v-bind:key="item.TITLE"
          v-on:click="navigateToMessage(item.id)"
        >
          <div class="preview-title">{{ item.title }}</div>
          <div class="preview-author">by {{ item.author }}</div>
          <div class="preview-details">
            <p>★★★★☆</p>
            <p>$14.95</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      items: [],
      id: "books"
    };
  },
  mounted() {
    this.pullItems();
  },
  methods: {
    pullItems: function() {
      this.$http.get("/api/books").then(
        response => {
          this.items = response.body;
        },
        error => {
          console.log(error);
        }
      );
    },
    navigateToMessage: function(id) {
      this.$router.push({ path: `/books/${id}` }).catch(err => {
        this.$router.push({ path: "/" });
      });
    }
  }
};
</script>

<style scoped>
.shop-content {
  margin-right: 10pt;
  border: 2px solid #a8dadc;
  border-radius: 4pt;
  background: #d3edee;
  padding: 0 10pt;
  margin-top: 10pt;
}
.preview-title {
  font-weight: bold;
  margin: auto;
  text-align: center;
  overflow-wrap: break-word;
}
.preview-author {
  font-size: 10pt;
  text-align: center;
}
.preview {
  border-radius: 4pt;
  border: 1px solid #fff;
  background: #fff;
  padding: 4px;
}
li {
  display: inline-block;
  width: 200px;
  height: 120px;
  overflow: hidden;
  margin: 5px;
}
h3 {
  text-align: center;
}
.shop {
  flex-grow: 1;
  margin-left: 220pt;
}
p {
  text-align: center;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}
</style>



